import * as React from 'react';
import { UiPathRobot, uipathRobot } from '@uipath/robot';

const UiPathAttendedRobots = (props) => {
  const processes = UiPathRobot.getProcesses

  return (
    <span>
      {processes}
    </span>
  )

}

const uiPathAttendedRobotsPlugin = {
  match: 'attended-robots',
  component: UiPathAttendedRobots
}

if (!window.cognigyWebchatMessagePlugins) {
  window.cognigyWebchatMessagePlugins = []
}

window.cognigyWebchatMessagePlugins.push(uiPathPlugin);